<template>

	<view>
		<meta name="theme-color" content="#F8F8F8">
		<view-logs />
		</meta>
		<view v-if="!isEditable" class="edit-tip">
			<text>ÂΩìÂâçÁä∂ÊÄÅ‰∏çÂèØÁºñËæë</text>
		</view>

		<!-- ‰∏ä‰º†ÂõæÁâáÂàóË°® -->
		<view style="width: 100%;overflow: hidden;">

			<scroll-view scroll-x="true" class="upload_box" ll-with-animation="true" :show-scrollbar="false">
				<view class="upload_item" v-for="(item, index) in uploadList" :key="index">
					<image :src="item" class="uploaded_image" @tap="viewFullImage" mode="aspectFill" />
					<image v-if="isEditable" src="/static/cancel.png" class="close_icon" @tap="deleteImage(index)" />
				</view>
				<view class="uploadImageBox" style="background: #f8f8f8;" v-if="isEditable">
					<image src="/static/add2.png" class="upload_img" @tap="selectImage(index)"
						style="width: 50px;height: 50px;margin: 25px;" />
				</view>

			</scroll-view>
		</view>
		<!-- Ê†áÈ¢ò -->
		<input v-model="name" type="text" :disabled="!isEditable" placeholder="ËØ∑ËæìÂÖ•Ê†áÈ¢ò"
			style="padding: 10px;margin: 20px 15px 5px 15px;display: block;">
		<view class="oneLine"></view>
		<textarea v-model="description" :disabled="!isEditable" placeholder="ËØ∑ËæìÂÖ•ÊèèËø∞"
			style="padding: 10px;margin:10px 15px 5px 15px;display: block;line-height: 28px;width: calc(100% - 50px);"></textarea>
		<view class="oneLine"></view>
		<view class="">
			<view class="relation-trigger" v-if="isEditable" @tap="showRelationPicker">
				<text class="placeholder">ÁÇπÂáªÂÖ≥ËÅîÂ®ÉÁâ©</text>
				<image src="/static/right2.png" class="arrow-icon" />
			</view>
		</view>
		<view class="publish-detail">
			<text>* Â±ïÁ§∫ÊÇ®ÁöÑÂÆùÂÆù‰ª¨ ü©µ„ÄÇ</text>
			<text>* ‰∏çÂÖ≥ËÅîÂïÜÂìÅÁöÑÂ±ïÁ§∫Êüú‰∏ç‰ºöÂá∫Áé∞Âú®ÂπøÂú∫‰∏≠„ÄÇ</text>
		</view>
		<!-- Áõ∏ÂÖ≥ -->
		<view class="saveCollocationDataList">
			<view v-for="(item, index) in saveCollocationDataList" :key="index">
				<view class="saveCollocationDataItem">
					<image v-if="item.goods_image" :src="item.goods_image" mode="aspectFill"
						style="width: 70px;height: 70px;"></image>
					<text v-else class="no-image">?</text>
					<text class="info-tap" style="width: calc(100% - 120px);display: inline-block;">{{item.type}}
						{{item.brand_name}} -
						{{item.goods_name}} </text>
					<image src="/static/cancel.png" v-if="isEditable" class="close_icon"
						@tap="deleteCollcation(index)" />
				</view>
			</view>
		</view>


		<relation-picker v-model:visible="showSelectTab" :typeList="typeList" :goodsList="goodsList"
			@confirm="handleRelationConfirm" @cancel="handleRelationCancel" />


		<view class="footer">
			<button v-if="showDelete" @click="handleDelete" class="delete-btn">Âà†Èô§</button>
			<button @click="submitForm">ÂèëË°®</button>
		</view>

	</view>
</template>

<script setup>
	import {
		onMounted,
		ref,
		computed,
	} from 'vue';
	import {
		onLoad
	} from '@dcloudio/uni-app';
	import {
		websiteUrl,
		image1Url,
		wechatSignLogin,
		getUserInfo,
		global,
		asyncGetUserInfo,
		getScene,
	} from "../../../common/config.js";

	import {
		chooseImage,
		jumpToCroper,
		getQiniuToken,
		uploadImageToQiniu,
		chooseImageList,
	} from "../../../common/image.js";

	//Êé•ÂèóÂèÇÊï∞ propsGoodsId propsGoodsName propsBrandId propsBrandNameÂ¶ÇÊûúÊòØÈùûÁ©∫ÊàñÈùû0ÔºåÊ∑ªÂä†‰∏ÄÊù°
	const props = defineProps(["showcase_id"])

	const uploadList = ref([]);
	const goodsList = ref([
		// {"name":"aa", "id":1}
	]);
	const typeList = ref([]);
	const showSelectTab = ref(false);
	// // ‰ø°ÊÅØËæìÂÖ•Ê°Ü‰∏¥Êó∂‰ø°ÊÅØ
	const chooseBrandName = ref("")
	const chooseBrandId = ref(0)
	const chooseGoodsName = ref("")
	const chooseGoodsId = ref(0)
	const chooseType = ref("")



	// Â§ÑÁêÜÁ°ÆËÆ§‰∫ã‰ª∂
	// const handleRelationConfirm = (selectedData) => {
	//   // ËøôÈáåÂ§ÑÁêÜÈÄâÊã©ÂêéÁöÑÊï∞ÊçÆ‰øùÂ≠òÈÄªËæë
	//   console.log('Êî∂Âà∞ÈÄâÊã©Êï∞ÊçÆ:', selectedData)
	//   // Â∞ÜÊï∞ÊçÆÊ∑ªÂä†Âà∞ saveCollocationDataList
	//   saveCollocationDataList.value.push({
	// 	brand_id: selectedData.brand_id,
	// 	goods_id: selectedData.goods.id,
	// 	brand_name: selectedData.brand.name,
	// 	goods_name: selectedData.goods.name,
	// 	goods_image: selectedData.goods.image,
	// 	type: selectedData.type
	//   })
	// }
	const handleRelationConfirm = (data) => {
		try {
			const relationData = {
				goods_id: data.goods.id || 0,
				goods_name: data.goods.name,
				goods_image: data.goods.image || '',
				brand_id: data.brand.id || 0,
				brand_name: data.brand.name || (data.isFuzzy ? '' : 'Êú™Áü•ÂìÅÁâå'),
				type: data.type || (data.isFuzzy ? 'Êú™Áü•Á±ªÂûã' : '')
			}

			// ÂéªÈáçÊ£ÄÊü•ÔºàÂêåÊó∂ÂåπÈÖçIDÂíåÂêçÁß∞Ôºâ
			const isExist = saveCollocationDataList.value.some(item =>
				(item.goods_id !== 0 && item.goods_id === relationData.goods_id) ||
				item.goods_name === relationData.goods_name
			)

			if (!isExist) {
				saveCollocationDataList.value.push(relationData)
			} else {
				uni.showToast({
					title: 'Â∑≤Â≠òÂú®Áõ∏ÂêåÂÖ≥ËÅîÈ°π',
					icon: 'none'
				})
			}
		} catch (error) {
			console.error('‰øùÂ≠òÂÖ≥ËÅîÊï∞ÊçÆÂ§±Ë¥•:', error)
			uni.showToast({
				title: '‰øùÂ≠òÂÖ≥ËÅî‰ø°ÊÅØÂ§±Ë¥•',
				icon: 'none'
			})
		}
	}
	// Â§ÑÁêÜÂèñÊ∂à‰∫ã‰ª∂
	const handleRelationCancel = () => {
		console.log('Áî®Êà∑ÂèñÊ∂àÈÄâÊã©')
	}

	const showRelationPicker = () => {
		showSelectTab.value = true;
	}

	// ÊòØÂê¶ÂèØÁºñËæë
	const isEditable = computed(() => [-1, 1, 3].includes(display.value));
	// const displayText = computed(() => displayOptions.value[display.value]);
	const showDelete = computed(() => {
		if (props.showcase_id > 0) {
			return true
		}
		return false
	});


	// ‰øÆÊîπÂìçÂ∫îÂºèÊï∞ÊçÆ
	const name = ref("")
	const description = ref("")
	const display = ref(-1) // 0=ÂÆ°Ê†∏‰∏≠


	//Ëé∑ÂèñÂïÜÂìÅËØ¶ÊÉÖ
	function getGoodsInfo(id) {
		return new Promise((resolve, reject) => {
			uni.request({
				url: websiteUrl + '/goods?id=' + id,
				method: 'GET',
				timeout: 5000,
				success: (res) => {
					console.log(res.data.data);
					resolve(res.data); // ËØ∑Ê±ÇÊàêÂäüÔºåËøîÂõûÊï∞ÊçÆ
				},
				fail: (err) => {
					console.log(err);
					uni.showToast({
						title: 'ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•',
						icon: 'none'
					})
					reject(err); // ËØ∑Ê±ÇÂ§±Ë¥•ÔºåÊäõÂá∫ÈîôËØØ
				},
				complete: () => {
					uni.hideLoading()
				}
			})
		});
	}

	//Â≠òÂÇ®ÁöÑ‰ø°ÊÅØÊ°ÜÂÜÖÂÆπÂàóË°®
	const saveCollocationDataList = ref([])


	// Ëé∑ÂèñShowcaseËØ¶ÊÉÖ
	async function getShowCaseInfo() {
		if (!props.showcase_id) return


		try {
			const res = await uni.request({
				url: `${websiteUrl}/with-state/showcase-detail?id=${props.showcase_id}`,
				method: 'GET',
				header: {
					'Authorization': uni.getStorageSync('token'),
				},
			})

			const data = res.data.data
			name.value = data.name
			description.value = data.description
			display.value = data.display
			uploadList.value = data.image_urls?.split(',') || [] // Á°Æ‰øùÂ§ÑÁêÜÁ©∫ÂÄº
			console.log(data)
			// Â¶ÇÊûúÊúâÂÖ≥ËÅîÂïÜÂìÅÈúÄË¶ÅÂä†ËΩΩ
			if (data.relations) {
				saveCollocationDataList.value = data.relations.map(r => ({
					goods_id: r.relation_goods_id,
					goods_name: r.relation_goods_name,
					brand_id: r.relation_brand_id,
					brand_name: r.relation_brand_name,
					type: r.type,
					goods_image: r.relation_goods_image
				}))
			}
		} catch (err) {
			uni.showToast({
				title: 'Âä†ËΩΩÂ§±Ë¥•',
				icon: 'none'
			})
		}
	}

	function viewFullImage(index) {
		uni.previewImage({
			current: uploadList.value[index],
			urls: uploadList.value
		});
	}

	//ÈÄâÊã©ÂõæÁâá
	async function selectImage() {
		try {
			// ÈÄâÊã©Â§öÂº†ÂõæÁâá
			const imagePaths = await chooseImageList(9);

			// ÈÄê‰∏™‰∏ä‰º†
			for (const path of imagePaths) {
				// ‰∏∫ÊØè‰∏™ÂõæÁâáËé∑ÂèñÁã¨Á´ãtoken
				const tokenData = await getQiniuToken();

				// ‰∏ä‰º†Âà∞‰∏ÉÁâõ‰∫ë
				await uploadImageToQiniu(path, tokenData.token, tokenData.path);

				// Ê∑ªÂä†Âà∞Â±ïÁ§∫ÂàóË°®
				uploadList.value.push(image1Url + tokenData.path);
			}

			uni.showToast({
				title: `ÊàêÂäü‰∏ä‰º†${imagePaths.length}Âº†ÂõæÁâá`,
				icon: 'success'
			});
		} catch (error) {
			console.error('‰∏ä‰º†Âá∫Èîô:', error);
			uni.showToast({
				title: 'ÈÉ®ÂàÜÂõæÁâá‰∏ä‰º†Â§±Ë¥•',
				icon: 'none'
			});
		}
	}

	async function handleDelete() {
		uni.showModal({
			title: 'Á°ÆËÆ§Âà†Èô§',
			content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™Â±ïÁ§∫ÂêóÔºü',
			success: async (res) => {
				if (res.confirm) {
					try {
						const res = await uni.request({
							url: `${websiteUrl}/with-state/delete-showcase?id=` + props
								.showcase_id,
							method: 'POST',

							header: {
								'Authorization': uni.getStorageSync('token'),
							}
						})

						if (res.data.status === "success") {
							uni.showToast({
								title: 'Âà†Èô§ÊàêÂäü'
							})
							setTimeout(() => uni.navigateBack(), 1000)
						}
					} catch (err) {
						uni.showToast({
							title: 'Âà†Èô§Â§±Ë¥•',
							icon: 'none'
						})
					}
				}
			}
		})
	}


	// ÂÆûÁé∞Êèê‰∫§ÊñπÊ≥ï
	async function submitForm() {
		const token = uni.getStorageSync('token');
		if (!token) {
			uni.showToast({
				title: 'ËØ∑ÂÖàÁôªÂΩï',
				icon: 'none'
			});
			return;
		}
		// Âü∫Êú¨È™åËØÅ
		if (!name.value.trim() || !description.value.trim()) {
			uni.showToast({
				title: 'Ê†áÈ¢òÂíåÊ≠£Êñá‰∏çËÉΩ‰∏∫Á©∫',
				icon: 'none'
			});
			return;
		}
		if (uploadList.value.length === 0) {
			uni.showToast({
				title: 'ËØ∑Ëá≥Â∞ë‰∏ä‰º†‰∏ÄÂº†ÂõæÁâá',
				icon: 'none'
			});
			return;
		}
		let scene = getScene()
		let postData = {
			name: name.value,
			description: description.value,
			image_urls: uploadList.value.join(','),
			display: display.value,
			origin: scene,
			relations: saveCollocationDataList.value.map(item => ({
				relation_goods_id: item.goods_id,
				relation_goods_name: item.goods_name,
				relation_brand_id: item.brand_id,
				relation_brand_name: item.brand_name,
				type: item.type,
				relation_origin: 2 // Ê†áËØÜÂÖ≥ËÅîÁöÑÊòØshowcase
			}))
		};

		try {
			let url = `${websiteUrl}/with-state/add-showcase`
			// id ËΩ¨int
			if (props.showcase_id) {
				url = `${websiteUrl}/with-state/update-showcase`
				postData = {
					...postData,
					id: parseInt(props.showcase_id, 10)
				}
			}

			const res = await uni.request({
				url,
				method: 'POST',
				data: postData,
				header: {
					'Content-Type': 'application/json',
					'Authorization': uni.getStorageSync('token'),
				}
			})

			if (res.data.code !== "failed") {
				uni.showToast({
					title: 'Êèê‰∫§ÊàêÂäü'
				})
				setTimeout(() => uni.navigateBack(), 1500)
			} else {
				uni.showToast({
					title: 'Êèê‰∫§Â§±Ë¥•',
					icon: 'none'
				})
				return
			}
		} catch (err) {
			console.log(err)
			uni.showToast({
				title: 'Êèê‰∫§Â§±Ë¥•',
				icon: 'none'
			})
		}


	}



	// Ëé∑ÂèñtypesÂàóË°®
	function getTypes() {
		///goods-types
		uni.request({
			url: websiteUrl + '/goods-types',
			method: 'GET',
			timeout: 5000,
			success: (res) => {
				console.log(res.data.data);
				typeList.value = res.data.data
			},
			fail: (err) => {
				console.log(err);
				uni.showToast({
					title: 'ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•',
					icon: 'none'
				})
			}
		})
	}

	// Â≠òÂÇ®‰∏ÄÊù°Êê≠ÈÖç‰ø°ÊÅØ
	function saveCollocation() {
		if (chooseBrandName.value == "") {
			uni.showToast({
				title: 'ËØ∑ÈÄâÊã©ÊàñÂ°´ÂÜôÂìÅÁâåÂêçÁß∞',
				icon: 'none'
			})
			return
		}
		if (chooseGoodsName.value == "") {
			uni.showToast({
				title: 'ËØ∑ÈÄâÊã©ÊàñÂ°´ÂÜôÂïÜÂìÅÂêçÁß∞',
				icon: 'none'
			})
			return
		}

		//Â¶ÇÊûúÊ≤°Êúâgoods_idÔºåÂ≠òÂÇ®goods_nameËøõÂéªÂç≥ÂèØ
		if (chooseGoodsId.value == 0) {
			let data = {
				"brand_id": chooseBrandId.value,
				"goods_id": 0,
				"brand_name": chooseBrandName.value,
				"goods_name": chooseGoodsName.value,
				"goods_image": "",
				"type": chooseType.value,
			}
			saveCollocationDataList.value.push(data)
			showSelectTab.value = false
			chooseBrandId.value = 0
			chooseBrandName.value = ""
			chooseGoodsId.value = 0
			chooseGoodsName.value = ""
			chooseType.value = ""
			return
		}


		getGoodsInfo(chooseGoodsId.value).then((res) => {
			let data = {
				"brand_id": chooseBrandId.value,
				"goods_id": chooseGoodsId.value,
				"brand_name": chooseBrandName.value,
				"goods_name": chooseGoodsName.value,
				"goods_image": res.data.goods_images[0],
				"type": chooseType.value,
			}
			saveCollocationDataList.value.push(data)
			showSelectTab.value = false
			chooseBrandId.value = 0
			chooseBrandName.value = ""
			chooseGoodsId.value = 0
			chooseGoodsName.value = ""
			chooseType.value = ""
		})
	}

	function deleteImage(index) {
		uploadList.value.splice(index, 1)
	}

	function deleteCollcation(index) {
		saveCollocationDataList.value.splice(index, 1)
	}
	// ËÆæÁΩÆÊ†áÈ¢ò
	uni.setNavigationBarTitle({
		title: 'Â±ïÁ§∫Êüú'
	})



	
	onLoad(async (options) => {
		// Ëß£Êûê URL ÂèÇÊï∞
		if (options.goods_id && options.goods_name && options.brand_id && options.brand_name && options.type) {
			// Ëé∑ÂèñÂïÜÂìÅÂõæÁâá
			let goodsImage = '';
			try {
				const goodsInfo = await getGoodsInfo(parseInt(options.goods_id));
				goodsImage = goodsInfo.data.goods_images[0] || '';
			} catch (error) {
				console.error('Ëé∑ÂèñÂïÜÂìÅÂõæÁâáÂ§±Ë¥•:', error);
			}

			// Ê∑ªÂä†Âà∞ÂÖ≥ËÅîÂàóË°®
			saveCollocationDataList.value.push({
				brand_id: parseInt(options.brand_id, 10),
				goods_id: parseInt(options.goods_id, 10),
				brand_name: options.brand_name,
				goods_name: options.goods_name,
				goods_image: goodsImage,
				type: options.type
			});
		}
		getTypes();
		getShowCaseInfo();
	});
</script>

<style lang="less">
	.uploaded_image {
		width: 100px;
		height: 100px;
		margin-right: 10px;

	}

	.upload_box {
		// Ê†∏ÂøÉ‰øÆÊîπÁÇπÔºö
		width: 100%; // ÈÅøÂÖç‰ΩøÁî® 100vwÔºàÂèØËÉΩÂõ†ÊªöÂä®Êù°ÂØºËá¥ÂÆΩÂ∫¶Ê∫¢Âá∫Ôºâ
		white-space: nowrap; // Âº∫Âà∂Â≠êÂÖÉÁ¥†‰∏çÊç¢Ë°å
		overflow-x: auto; // Á°Æ‰øùÊ®™ÂêëÊªöÂä®ÁîüÊïàÔºàUniAppÁöÑscroll-viewÁªÑ‰ª∂Â∑≤Â∞ÅË£ÖÊªöÂä®ÈÄªËæëÔºåÊ≠§Â§Ñ‰∏∫‰øùÈô©ÂèØ‰øùÁïôÔºâ
		display: block; // ÊòéÁ°ÆÂÆπÂô®‰∏∫ÂùóÁ∫ßÂÖÉÁ¥†
		margin-top: 15px;
		padding: 10px;
		margin-left: 10px;

		view {
			display: inline-block; // ÂÖ≥ÈîÆÔºÅÂº∫Âà∂Â≠êÂÖÉÁ¥†Ê®™ÂêëÊéíÂàóÔºàÊõø‰ª£ floatÔºâ
			vertical-align: top; // ÂØπÈΩêÊñπÂºèÔºàÈÅøÂÖçÂõæÁâáÂ∫ïÈÉ®ÁïôÁôΩÔºâ
		}

		.upload_item {
			position: relative;
			width: 100px;
			margin-right: 10px;

			border-radius: 5px;
			overflow: hidden;

			.uploaded_image {
				width: 100px;
				height: 100px;
			}

			.close_icon {
				position: absolute;
				right: 0;
				top: 0;
				width: 20px;
				height: 20px;
			}
		}
	}

	.saveCollocationDataList {
		padding-bottom: 80px;
		min-height: 300rpx;

		.saveCollocationDataItem {
			position: relative;
			background: #fafafa;
			width: 90vw;
			margin: 10px auto;

			.info-tap {
				position: absolute;
				bottom: 30px;
				margin-left: 10px;
			}

			.no-image {
				display: inline-block;
				width: 70px;
				height: 70px;
				bottom: 0px;
				margin-left: 0px;
				color: #fda7a7;
				font-size: 30px;
				text-align: center;
				line-height: 70px;
				font-weight: 1000;
			}

			.close_icon {
				width: 20px;
				height: 20px;
				position: absolute;
				bottom: 30px;
				right: 10px;
				margin-left: 10px;
				background: #fff;
				border-radius: 100%;
			}
		}


	}

	.footer {
